:root {
  --color-primary: #f38e82;
  --color-grad-1: #fbdb89;
  --color-grad-2: #f48982;
  --color-grey-light-1: #f9f5f3;
  --color-grey-light-2: #f2efee;
  --color-grey-light-3: #d3c7c3;
  --color-grey-dark-1: #615551;
  --color-grey-dark-2: #918581;
  --gradient: linear-gradient( to right bottom, var(--color-grad-1), var(--color-grad-2));
  --bp-large: 1250px;
  --bp-medium: 980px;
  --bp-small: 600px;
  --bp-smallest: 500px;
}

* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--color-grey-dark-1);
  background-image: var(--gradient);
  min-height: calc(100vh - 8vw);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  line-height: 1.6;
}

.container {
  max-width: 100%;
  min-height: 117rem;
  box-shadow: 0 2rem 6rem .5rem rgba(var(--color-grey-dark-1), .2);
  background-color: #fff;
  border-radius: 9px;
  margin: 2vw auto;
  overflow: hidden;
}

.header {
  background-color: var(--color-grey-light-1);
  grid-area: head;
  justify-content: space-between;
  align-items: center;
  padding: .3em;
}

.header__logo {
  height: 4.6rem;
  margin-left: 1em;
  display: block;
}

.search {
  background-color: #fff;
  border-radius: 10rem;
  justify-content: space-between;
  align-items: center;
  margin: 1em;
  padding-left: 2em;
  transition: all .3s;
  display: flex;
}

.search:focus-within {
  box-shadow: 0 .7rem 3rem rgba(var(--color-grey-dark-1), .08);
  transform: translateY(-2px);
}

.search__field {
  width: 80%;
  max-width: 40rem;
  color: inherit;
  background: none;
  border: none;
  border-radius: 10em;
  outline: none;
  margin-right: .5em;
  padding: .7em 1em;
  font-family: inherit;
}

.search:focus {
  border: none;
  outline: none;
}

.search::placeholder {
  color: var(--color-grey-light-3);
}

.search__btn {
  background-image: var(--gradient);
  padding-right: 0;
  font-family: inherit;
  font-weight: 600;
  display: block;
}

.nav {
  align-self: stretch;
}

.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__btn {
  height: 100%;
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  padding: 0 1em;
  font-family: inherit;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

svg {
  height: 1.5rem;
  width: 1.5rem;
  fill: currentColor;
}

.nav:focus {
  outline: none;
}

.nav:hover {
  background-color: var(--color-grey-light-2);
}

.bookmarks {
  z-index: 10;
  width: max-content;
  max-width: 40rem;
  max-height: 75vh;
  box-shadow: 0 .8rem 5rem 2rem rgba(var(--color-grey-dark-1), .1);
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  padding: 1rem 0;
  transition: all .5s .2s;
  position: absolute;
  overflow-y: auto;
}

.bookmarks__list {
  list-style: none;
}

.bookmarks__field {
  cursor: pointer;
  height: 100%;
  align-items: center;
  padding: 0 4rem;
  transition: all .3s;
  display: flex;
}

.bookmarks:hover {
  background-color: var(--color-grey-light-2);
}

.bookmarks:hover, .nav__btn--bookmarks:hover + .bookmarks {
  visibility: visible;
  opacity: 1;
}

button {
  background-color: var(--gradient);
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

button:hover {
  transform: scale(1.05);
}

button:focus {
  outline: none;
}

button > :first-child {
  margin-right: 1rem;
}

.btn {
  padding: 1em 2em;
  font-size: 1.5rem;
  font-weight: 600;
  display: flex;
}

.btn svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: currentColor;
}

.btn--small, .btn--small:link, .btn--small:visited {
  padding: 1.25rem 2.25rem;
  font-size: 1.4rem;
  font-weight: 600;
  text-decoration: none;
}

.btn-small svg {
  height: 1.75rem;
  width: 1.75rem;
  fill: currentColor;
}

.btn--inline {
  color: var(--color-primary);
  background-color: var(--color-grey-light-1);
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10rem;
  align-items: center;
  padding: .8rem 1.2rem;
  font-size: 1.3rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.btn--inline svg {
  height: 1.6rem;
  width: 1.6rem;
  fill: currentColor;
  margin: 0 .2rem;
}

.btn--inline span {
  margin: 0 .4rem;
}

.btn--inline:hover {
  color: var(--color-grad-2);
  background-color: var(--color-grey-light-2);
}

.btn--inline:focus {
  outline: none;
}

.btn--round {
  background-image: var(--gradient);
  cursor: pointer;
  height: 4.5rem;
  width: 4.5rem;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn--round:hover {
  transform: scale(1.07);
}

.btn--round:focus {
  outline: none;
}

.btn--round svg {
  height: 2.5rem;
  width: 2.5rem;
  fill: #fff;
  margin: auto;
}

.btn--tiny {
  height: 2.5rem;
  width: 2.5rem;
  cursor: pointer;
  background: none;
  border: none;
}

.btn--tiny svg {
  height: 100%;
  width: 100%;
  fill: var(--color-primary);
  margin: 0;
  transition: all .3s;
}

.btn--tiny:focus {
  outline: none;
}

.btn--tiny:hover svg {
  fill: var(--color-grad-2);
  transform: translateY(-1px);
}

.btn--tiny:active svg {
  fill: var(--color-grad-2);
  transform: translateY(0);
}

.btn--tiny:not(:last-child) {
  margin-right: .3rem;
}

.heading--2 {
  color: var(--color-primary);
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  font-weight: 700;
}

.link:link, .link:visited {
  color: var(--color-grey-dark-2);
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner svg {
  height: 6rem;
  width: 6rem;
  fill: var(--color-primary);
  animation: rotate 2s linear infinite;
}

.message, .error {
  max-width: 40rem;
  margin: 0 auto;
  padding: 5rem 4rem;
  display: flex;
}

.message svg, .error svg {
  height: 3rem;
  width: 3rem;
  fill: var(--color-primary);
  transform: translateY(-.3rem);
}

.message p, .error p {
  margin-left: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 1.5;
}

.preview__link:link, .preview__link:visited {
  border-right: 1px solid #fff;
  align-items: center;
  padding: 1.5rem 3.25rem;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.preview__link:hover {
  background-color: var(--color-grey-light-1);
  transform: translateY(-2px);
}

.preview__link--active {
  background-color: var(--color-grey-light-1);
}

.preview__fig {
  height: 4.5rem;
  backface-visibility: hidden;
  border-radius: 50%;
  flex: 0 0 4.5rem;
  margin-right: 2rem;
  position: relative;
  overflow: hidden;
}

.preview:before {
  content: "";
  height: 100%;
  width: 100%;
  z-index: -1;
  background-image: linear-gradient( to right bottom, var(--color-grad-1), var(--color-grad-2));
  opacity: .4;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all .3s;
  display: block;
}

.preview__data {
  width: 100%;
  grid-template-columns: 1fr 2rem;
  align-items: center;
  row-gap: .1rem;
  display: grid;
}

.preview__title {
  color: var(--color-primary);
  text-transform: uppercase;
  text-overflow: ellipsis;
  max-width: 23rem;
  white-space: nowrap;
  grid-column: 1 / -1;
  font-size: 1.3rem;
  font-weight: 600;
  overflow: hidden;
}

.preview__publisher {
  color: var(--color-grey-dark-2);
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
}

.preview__user-generated {
  background-color: darken(var(--color-grey-light-2), 2%);
  height: 2rem;
  width: 2rem;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 1.75rem;
  display: flex;
}

.preview svg {
  height: 1.2rem;
  width: 1.2rem;
  fill: var(--color-primary);
}

.recipe {
  background-color: var(--color-grey-light-1);
  grid-area: recipe;
}

.recipe__fig {
  height: 32rem;
  transform-origin: top;
  position: relative;
}

.recipe__fig:before {
  content: "";
  height: 100%;
  width: 100%;
  background-image: linear-gradient( to right bottom, var(--color-grad-1), var(--color-grad-2));
  opacity: .6;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.recipe__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.recipe__title {
  color: #fff;
  text-transform: uppercase;
  width: 50%;
  text-align: center;
  font-size: 3.25rem;
  font-weight: 700;
  line-height: 1.95;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 20%)skewY(-6deg);
}

.recipe__title span {
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  background-image: linear-gradient( to right bottom, var(--color-grad-1), var(--color-grad-2));
  padding: 1.3rem 2rem;
}

.recipe__details {
  flex-direction: column;
  align-items: center;
  gap: 2em;
  padding: 7em 8em 3em;
  display: flex;
}

.recipe__info {
  text-transform: uppercase;
  align-items: center;
  font-size: 1.65rem;
  display: flex;
}

.recipe__info:not(:last-child) {
  margin-right: 4.5rem;
}

.recipe__info-icon {
  height: 2.35rem;
  width: 2.35rem;
  fill: var(--color-primary);
  margin-right: 1.15rem;
}

.recipe__info-data {
  margin-right: .5rem;
  font-weight: 700;
}

.recipe__info-buttons {
  justify-content: space-around;
  margin-left: 1.5rem;
  display: flex;
  transform: translateY(-1px);
}

.recipe__user-generated {
  background-color: darken(var(--color-grey-light-2), 2%);
  height: 4rem;
  width: 4rem;
  border-radius: 10rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.recipe__user-generated svg {
  height: 2.25rem;
  width: 2.25rem;
  fill: var(--color-primary);
}

.recipe__ingredients {
  background-color: var(--color-grey-light-2);
  flex-direction: column;
  align-items: center;
  padding: 5rem 8rem;
  font-size: 1.6rem;
  line-height: 1.4;
  display: flex;
}

.recipe__ingredient-list {
  justify-content: center;
  gap: 2.5rem 3rem;
  list-style: none;
  display: grid;
}

.recipe__ingredient {
  display: flex;
}

.recipe__icon {
  height: 2rem;
  width: 2rem;
  fill: var(--color-primary);
  flex: none;
  margin-top: .1rem;
  margin-right: 1.1rem;
}

.recipe__quantity {
  flex: none;
  margin-right: .5rem;
}

.recipe__directions {
  flex-direction: column;
  align-items: center;
  padding: 5rem 10rem;
  display: flex;
}

.recipe__directions-text {
  text-align: center;
  color: var(--color-grey-dark-2);
  margin-bottom: 3.5rem;
  font-size: 1.7rem;
}

.recipe__directions a {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.recipe__publisher {
  font-weight: 700;
}

.search-results {
  flex-direction: column;
  grid-area: list;
  padding: 3rem 0;
  display: flex;
}

.results {
  height: 40vh;
  grid-template-columns: 1fr 1fr;
  gap: .3em;
  margin-bottom: 2rem;
  list-style: none;
  display: grid;
  overflow-y: auto;
}

.pagination {
  margin-top: auto;
  padding: 0 3.5rem;
}

.pagination:after {
  content: "";
  clear: both;
  display: table;
}

.pagination__btn--prev {
  float: left;
}

.pagination__btn--next {
  float: right;
}

.copyright {
  color: var(--color-grey-dark-2);
  margin-top: 4rem;
  padding: 0 3.5rem;
  font-size: 1.2rem;
}

.copyright .twitter-link:link, .copyright .twitter-link:visited {
  color: var(--color-grey-dark-2);
}

.add-recipe-window {
  width: 95vw;
  z-index: 1000;
  background-color: #fff;
  border-radius: 9px;
  padding: 5rem 6rem;
  transition: all .5s;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 4rem 6rem #00000040;
}

.add-recipe-window .btn--close-modal {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  font-family: inherit;
  font-size: 3.5rem;
  position: absolute;
  top: .5rem;
  right: 1.6rem;
}

.overlay {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  z-index: 100;
  background-color: #0006;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.upload {
  height: 85vh;
  grid-template-columns: 1fr;
  gap: 4rem 6rem;
  display: grid;
  overflow-y: scroll;
}

.upload__column {
  width: 100%;
  grid-template-columns: 1fr;
  align-items: center;
  gap: 1.5em;
  display: grid;
}

.upload__column .item {
  justify-content: flex-start;
  align-items: center;
  gap: .3em;
  display: flex;
}

.upload__column .item label {
  color: inherit;
  flex: 1 0 8rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.upload__column .item input {
  border: 1px solid #ddd;
  border-radius: .5rem;
  flex: 2 8rem;
  padding: .8rem 1rem;
  font-size: 1.5rem;
  transition: all .2s;
}

.upload__column .item input::placeholder {
  color: var(--color-grey-light-3);
}

.upload__column .item input:focus {
  border: 1px solid var(--color-primary);
  background-color: var(--color-grey-light-1);
  outline: none;
}

.upload__column button {
  grid-column: 1 / span 2;
  justify-self: center;
  margin-top: 1rem;
}

.upload__heading {
  text-transform: uppercase;
  grid-column: 1 / -1;
  margin-bottom: 1rem;
  font-size: 2.25rem;
  font-weight: 700;
}

.upload__btn {
  grid-column: 1 / -1;
  justify-self: center;
}

@media (min-width: 600px) {
  .header__logo {
    margin-left: 1em;
  }

  .search__field {
    font-size: 1.5rem;
  }
}

@media (min-width: 769px) {
  .container {
    margin: 3vw auto;
  }

  .header {
    justify-content: space-between;
    display: flex;
  }

  .bookmarks {
    max-height: 84vh;
